body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


header {
  width: 750px;
}

h1, h2 {
  color: #000000;
  font-family: 'rubikregular';
  text-align: center;
}

#readingBingoHeader {
  margin-bottom: 50px;
}

#sheetContent, #user-login-form {
  margin-top: 35px;
}

#readingBingo {
  margin-left: 20px;
}

#readingBingoKey {
  margin-top: 250px;
  font-family: 'rubikregular';
  size: 20px;
}

#bingoKeyList {
  margin-left: 150px;
}

.square {
  stroke-width: 1px;
  stroke: #000000;
}

.header-square {
  fill: #EEDDCC;
}

