.square0 {
  fill: #B4B387;
}

.square1 {
   fill: #EEDDCC;
}

.square2 {
   fill: #778AAB;
}

.square3 {
   fill: #BCAAAA;
}

.square4 {
   fill: #DDBB98;
}

text {
  color: black;
  font-size: 15px;
  text-anchor: "middle";
  font-family: "rubikregular";
  font-weight: bold;
}

.bingo-text {
  font-size: 48px;
}

